/* Top_btn */
.top_btn{float:right;margin-right:18px;
	a, a:active, a:visited{color:#999; text-decoration:none}
}

// Scroll BTN
.scroll_btn{position:fixed; right:10px; bottom:10px; background-color:#fff; box-shadow: -2px 4px 7px -2px rgba(0,0,0,0.6);
	a{display:block; width:35px; height:20px; line-height:20px; text-align:center;text-decoration:none; font-size:15px;color:#999; font-weight:bold; border:1px solid #dedede; border-bottom:none;font-weight:bold; 
		&:first-child{border-top-left-radius:5px;}
		&:last-child{border-bottom:1px solid #dedede;/* border-bottom-left-radius:5px; */}
		&.btn_top, &.btn_bottom{font-size:11px;}
	}
	a:hover{background-color:#efefef;transition: all ease-in-out 0.2s;}
	&:hover{box-shadow: -2px 4px 7px -2px rgba(0,0,0,0.3);transition: all ease-in-out 0.1s;}
}


//버튼 
.btn{display:inline-block;
	span{display:inline-block;position:relative;min-width:59px; margin:1px 0; padding:2px 2px; border:1px solid #dedede;border-radius:5px;text-align:center; background-color:#eee;color:#000; }
	&.active span{border:1px solid #6db8eb;  background-color:#6db8eb; }
	&.active:hover span{background-color:#6db8eb; }
	&.active span{color:#fff; cursor: default;}
	&.disable span{background-color:#eee;}
	&.disable span{color:#ccc;}
	&.disable:hover span{background-color:#eee; }
	&.disable a{cursor:default;}
	&:hover span{background-color:#ddd; transition: all ease-in-out 0.2s;}

	// Filter 관련 
	&.bullet{
		span{ padding:2px 2px 2px 9px; text-align:left; background-color:#fff;}
		span:after{position:absolute;top:7px; left:3px; content:''; width:4px;height:4px;  background-color:#fff;}
	}
	&.on{
		span{color:#fff;}
	}
	&.del{@include filterBtn($color-del);}//삭제
	&.equal{@include filterBtn($color-equal);}//동일
	&.hold{@include filterBtn($color-hold);}//보류
	&.rework{@include filterBtn($color-rework);}//재확인
	&.layer{@include filterBtn($color-layer);}//레이어팝업
	&.popup{@include filterBtn($color-popup);}//팝업
	&.new{@include filterBtn($color-new);}//신규
	&.result, &.result_ex, &.total, &.real{@include filterBtn_default($color-default);}//완료, 미완, 전체, 삭제제외
	&.real span{min-width:135px;text-align:center;}
}


