table{border-collapse:collapse;border-spacing:0;
	caption{visibility:hidden;width:0;height:0;font-size:0}
	th{background:#eee;border:1px solid #d1d1d1;padding:3px 0; letter-spacing: -0.8px}
	td{border:1px solid #d1d1d1;padding:3px 0 3px 5px; letter-spacing: -0.3px;
		a{color:#193d79;}
		&.path a{color:$color-path;}//링크색상
		&.center{text-align:center;padding-left:0;}
	}
	th, td{
		&.right{text-align:right;padding-right:10px;padding-left:0;}
	}
	tr.focus{background-color:$color-focus_line;}
}

/* Table_content */
.tab_contents_wrap{width:100%; margin-top:10px;/* background:#fff; */border-top:none;
	// table colgroup col{width:auto !important;}
	table{background-color:#fff;}
	table tr td:first-child{position:relative;}
	table tr:hover td{background-color:$color-hover_line;}
	table tr.focus:hover td{background-color:$color-focus_line;}
	table{
		td.etc{position:relative;padding-right:15px;}
		td.etc .moreBtn{position:absolute; top:3px;right:5px;color:#ccc;}
		// td.etc .moreBtn{right:15px;opacity:0;}
		td.etc .moreBtn.on{right:5px;opacity:1; transition: all  ease-in-out 0.26s}
		td.etc .moreBtn.on:hover{color:#666;}
		td.etc .desc{}
	}
}

//td.num style : ; childOption 일경우 
tr td.num{padding-right:4px;padding-left:0;text-align:right;}
body[data-childOption="true"] tr.child td.num{color:#aaa}
body[data-childOption="true"] tr.child td.num a{color:#aaa}

//dimmed
.dimmed{z-index:998;display:none;position:fixed;left:0; right:0; top:0;bottom:0;background-color:#000; opacity:0.2;}

// popUp
.popOn .dimmed{display:block;}
.popOn .popArea{display:block; top:53%; opacity:0;}
.popOn .popArea.motion{top:50%;transition: all  ease-in-out 0.2s;opacity:1;}
.popOn .popArea.motion_end{top:47%;transition: all  ease-in-out 0.2s;opacity:0;}

.popArea{z-index:999; display:none;position:fixed;left:50%; top:250%; transform:translate(-50%, -50%); min-width:150px;background-color:#fff; box-shadow: 0 0 15px 0 rgba(0,0,0,.5); border-radius: 6px; overflow:hidden; }


.popArea .popWrap{position:relative;min-width:200px;}
.popArea .popHead{position:relative;padding:10px 15px; padding-right:25px; font-size:16px; font-weight:bold;color:#333;line-height:17px;}
.popArea .popHead:after{content:''; position:absolute; bottom:0px; left:0px;right:0px;height:1px; background-color:#ddd; }
.popArea .popCon{padding:10px 15px 30px;max-height:400px;overflow-y:auto;}
.popArea .popCon .desc{position:relative;display:block;padding-left:5px;font-size:12px}
.popArea .popCon .desc:after{content:''; position:absolute; top:8px; left:0; width:2px;height:2px; background-color:#333;}
.popArea .popFoot{position:absolute;left:0;right:20px; bottom:0px; min-height:20px; background-color:#fff; /* box-shadow:-20px 2px 100px 0px rgba(0,0,0,0.2) */ }

.popArea .closeBtn{position:absolute;right:8px;top:4px;padding:3px 8px 4px; font-size:14px;transition: all  ease-in-out 0.26s; }
.popArea .closeBtn i{transition: all  ease-in-out 0.26s; transform:rotate(180deg) scale(1);color:#888;}
.popArea .closeBtn:hover{background-color:#eee;border-radius:4px;}
.popArea .closeBtn:hover i{ transition: all  ease-in-out 0.2s; transform:rotate(90deg) scale(1.1);color:#333;;}



