@charset "utf-8";


//Base (추후 이동)
*{margin:0;padding:0;}

.tab_contents_wrap h3{padding-bottom:5px;}



//Header
.header{position:relative;}


// 1Depth Menu : Tab_nav
.tab_nav{z-index:5;position:relative; margin:0; border-bottom:1px solid #ddd; background-color: #fff;text-align:left; box-shadow:'';padding-top:3px; overflow:hidden;


	h2{width:1px;height:1px;position:absolute;left:0;right:0;line-height:1px;letter-spacing:-999px;font-size:0;color:#fff;}
	.count{position: relative; top: -1px; margin: 0 0 0 5px; padding: 1px 5px 2px 5px; font-size: 10px; font-weight: bold; color: #666; background: #E5E5E5; border-radius: 10px; font:11px verdana, sans-serif; line-height:1.25;}

	& >ul{margin-left:10px;
		&:after{content:''; clear:both;display:table;}
		> li{float:left;margin-left:2px;/* margin-top:31px; */}// Sub menu Height :  스크립트로 제어
		> li:first-child{margin-left:0;}

		> li >	a{position:relative;display: block;margin-bottom: -1px; padding: 8px 12px 9px; border: 1px solid #ddd;border-radius: 3px 3px 0 0; font-size: 14px; color: #666; text-decoration: none;background: #fff; transition: all ease-in-out 0.2s;}

		/*활성화*/
		> li.on >a,
		> li.on:hover > a{ border:1px solid #bbb; border-bottom:1px solid #ddd; color:#fff; font-weight:bold; background:#bbb}

		> li.active >a,
		> li:hover > a{background:#efefef;}/* 2Depths over 시 1Depths 활성화*/

		//1Depths Hover Line Motion
		> li >a:after{position:absolute;content:''; bottom:-2px;left:50%;right:50%;height:0;background-color:#fff;transition: all ease-in-out 0.2s;}
		> li.active > a:after,
		> li:hover > a:after{bottom:0;left:0;right:0;height:3px;background-color:#bbb;}
		> li.on.active >a {background-color:#bbb;}

		/* 2Depths*/

		/* > li.on , li.on:hover{
			a{border:1px solid #bbb; border-bottom:1px solid #ddd; color: #fff; font-weight: bold; background: #bbb; transition: all ease-in-out 0.4s; }
		}
		li.active, li:hover{
			a{background: #efefef;}
			a:after{bottom:-1px; left:0; right:0; height:3px; background-color:#bbb;transition: all ease-in-out 0.2s;}
		}
		li.on.active{
			a{background-color:#bbb;}
		} */
	}

	//sub Nav
	li.active > .subNav{display:block;}

	.subNav{display:none;position:absolute;left:0;right:0;top:40px;margin-left:10px;
		> ul{position:relative;padding:0;
			&:after{content:''; clear:both; display:table;}

			li{float:left;}
			li a{position:relative;display:block;padding:5px 7px 7px;font-size:13px;color:#999; text-decoration:none; letter-spacing:-0.6px}
			li a:after{content:''; position:absolute;bottom:01px;left:50%;width:0%;height:2px;background-color:#bbb}
			li a:hover{color:#555;}
			li a:hover:after
			,li.active a:after{left:0;width:100%;transition:all ease-in-out 0.2s}
		} //ul
	}//subNav

	&.on ul{/* margin-bottom:0; */}
}




// Info_section
.info_section h2{padding:10px 0;}
.info_section{z-index:5;position:relative;@extend .clearfix; padding:0px 10px 10px; background-color:#f6f6f6; border:1px solid #efefef;border-top:none;
	& > div{
		.close_btn{display:inline-block;min-width:10px;padding:1px 2px 2px;color:#808080;border:1px solid #dedede; text-align:center; font-size:12px;line-height:11px; text-decoration:none; border-radius:5px;}
		&.hide {width:100%;min-height:auto;
			h2{margin-bottom:0;padding-bottom:0;}
			table{display:none;}
		}
	}
	table td{background-color:#fff;padding:3px  5px;}
	.info_box .history_area{width:95%; height:22px;overflow-y:auto; margin:3px 0 0;padding:4px;font-size:11px; color:#999; border:1px solid #ccc;}
	.info_box{float:left; padding-right:10px;}
	.link_box{float:left;padding-right:10px;}
	.filter_box{float:left; padding-right:10px;}

	// 검색
	#id_search{width:98%}
}

.info_section .fixed_btn{position:absolute; right:15px;top:10px; color:#666;
	&.on{color:#bbb;}
}


//Btn : open , close
.info_fold_btn{position:relative; text-align:right;
	a{z-index:8; position:absolute;top:-1px;right:10px;display:inline-block;width:60px;height:22px;line-height:20px;text-align:center; text-decoration:none; font-weight:bold; background-color:#f6f6f6;color:#999; border-bottom-left-radius:5px;border-bottom-right-radius:5px; border:1px solid #ddd;border-top:none; }
}
.headerFloating .info_fold_btn a{right:-10px;background-color:#f6f6f6;border:1px solid #ddd;}
.headerFloating .tab_nav{border-bottom:1px solid #f6f6f6;}
body[data-headerclose="false"] .info_fold_btn a{border-top:none;background-color:#f6f6f6;}

.tab_contents_wrap table tr.off{display: none;}
.tab_contents_wrap table tr.off2{display: none;}
.tab_contents_wrap table tr.off3{display: none;}
.tab_contents_wrap table tr.off4{display: none;}
.tab_contents_wrap table tr.off5{display: none;}
.tab_contents_wrap table tr.off6{display: none;}
.tab_contents_wrap table tr.off7{display: none;}
.tab_contents_wrap table tr.off8{display: none;}
.tab_contents_wrap table tr.off9{display: none;}

.tab_nav{position: relative;}
.tab_nav ul{position: relative;}
.tab_nav .btn_pin{position: absolute; top:50px; left:0px; color: #666;}

.dep_fold_btn{font-size: 11px;color: #999!important;}
h3 .dep_fold_btn{float: none;vertical-align: top;margin-right: 3px;}
.dep_fold_btn.on{color: #ddd !important;}
th .dep_fold_btn.on{color: #bbb !important;}

.dep_fold_btn.on{transition: all ease-in-out 0.26s}
.dep_fold_btn.on:hover{color:#666 !important;}


//GNB
.gnbOpenBtn{position:fixed;z-index:98;right:10px;top:5px;color:#333;font-size:22px;}
.gnbCloseBtn{position:absolute;right:15px;top:5px;color:#333;font-size:22px;} 

.gnb{display:none; position:fixed;z-index:99; left:0;top:0;right:0;bottom:0;background:#fff;}
.gnb.on{display:block;}
.gnb a{text-decoration:none;}

.gnbHead{background-color:#f5f5f5; padding-bottom:6px;
	.gnbTitle{position:relative;padding:10px;background-color:#fff;border-bottom:1px solid #ddd;}
	.gnbOption{}
}

.gnbBody{}

.gnbNav{display:block;position:absolute;z-index:1; top:50px;bottom:0;left:0;width:120px;background-color:#1e282c;overflow:auto;
	li:first-child{margin-top:15px;}
	li a{display:block;padding:6px 10px;font-size:12px;font-weight:bold;color:#b8c7ce; text-decoration:none;}

	li.on a, li a:hover{color:#fff;}
}

.gnbCon{z-index:1;position:absolute;top:50px;bottom:0;left:120px;right:0;overflow:auto;
	.conWrap{margin:15px 0 0 10px;
		.title_2dep{padding:5px 0 3px 5px; font-size:14px;font-weight:bold;border-bottom:1px solid #1e282c;}
		.menuWrap{padding-left:0;}
		.title_3dep{padding:5px 0 3px 5px; font-size:12px;font-weight:bold;border-bottom:1px solid #ccc;}
		.menuWrap li a{display:block;color:#000;padding:4px 5px 3px;border-bottom:1px solid #eee; font-size:11px;}
		.menuWrap .menuWrap li a{padding-left:10px;}
		.menuWrap .menuWrap .menuWrap li a{padding-left:20px;}
	}
}



